import React, { createContext, useReducer } from 'react';
import AppReducer from './AppReducer';
import axios from 'axios';
import { token } from '../components/token'
const _t = require('../components/token')

// const url_base = 'https://api.seguridad-acerinox.es/';

// Initial state
const initialState = {
  checklists: [],
  checklistInstances: [],
  latests: [],
  auditors: [],
  error: null,
  loading: true,
  validationError: null,
  successMessage: null
}
//DELETE
const _token = _t.token;
// Create context
export const GlobalContext = createContext(initialState);

// Provider component
export const GlobalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(AppReducer, initialState);

  // Actions
  async function getAuditors() {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'token': _token 
      }
    }
    try {
      const res = await axios.get('/api/v1/queries/auditors', config);

      dispatch({
        type: 'GET_AUDITORS',
        payload: res.data.data
      });
    } catch (err) {
      dispatch({
        type: 'TA_ERROR',
        payload: err.response.data.error
      });
    }
  }

  async function getChecklists() {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'token': _token 
      }
    }
    try {
      const res = await axios.get('/api/v1/checklists', config);

      dispatch({
        type: 'GET_CHECKLISTS',
        payload: res.data.data
      });
    } catch (err) {
      dispatch({
        type: 'TA_ERROR',
        payload: err.response.data.error
      });
    }
  }

  async function getChecklistInstances(type, page, pageSize) {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'token': _token 
      }
    }
    try {
      const res = await axios.get(`/api/v1/checklistInstances/${type}/${page}/${pageSize}`, config);

      dispatch({
        type: 'GET_CHECKLIST_INSTANCES',
        payload: res.data.data
      });
    } catch (err) {
      dispatch({
        type: 'TA_ERROR',
        payload: err.response.data.error
      });
    }
  }

  async function getLatests(quantity) {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'token': _token
      }
    }
    try {
      const res = await axios.get(`/api/v1/queries/latests/${quantity}`, config);

      dispatch({
        type: 'GET_LATESTS',
        payload: res.data.data
      });
    } catch (err) {
      dispatch({
        type: 'TA_ERROR',
        payload: err.response.data.error
      });
    }
  }

  async function deleteChecklistInstance(id) {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'token': _token 
      }
    }
    try {
      await axios.delete(`/api/v1/checklistInstances/${id}`, config);

      dispatch({
        type: 'DELETE_CHECKLIST_INSTANCE',
        payload: id
      });
    } catch (err) {
      dispatch({
        type: 'TA_ERROR',
        payload: err.response.data.error
      });
    }
  }

  async function addChecklist(checklist) {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'token': _token
      }
    }

    try {
      const res = await axios.post('/api/v1/checklists', checklist, config);

      dispatch({
        type: 'ADD_CHECKLIST',
        payload: res.data.data
      });
    } catch (err) {
      dispatch({
        type: 'TA_ERROR',
        payload: err.response.data.error
      });
    }
  }

  async function addChecklistInstance(checklistInstance) {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'token': _token 
      }
    }

    try {
      const res = await axios.post('/api/v1/checklistInstances', checklistInstance, config);

      dispatch({
        type: 'ADD_CHECKLIST_INSTANCE',
        payload: res.data.data
      });
    } catch (err) {
      dispatch({
        type: 'TA_ERROR',
        payload: err.response.data.error
      });
    }
  }
  
  async function updateChecklistInstanceStatus(checklistInstance, newStatus, extension) {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'token': _token 
      }
    }

    try {
      const res = await axios.put(`/api/v1/checklistInstances/${newStatus}/${extension}`, checklistInstance, config);
      console.log("globalstate update checklistInstance: ", JSON.stringify(checklistInstance));
      console.log("globalstate newStatus: ", newStatus);
      dispatch({
        type: 'UPDATE_CHECKLIST_INSTANCE',
        payload: res.data.data
      });
    } catch (err) {
      dispatch({
        type: 'TA_ERROR',
        payload: err.response.data.error
      });
    }
  }
  function resetError(){
    dispatch({
      type: 'TA_ERROR',
      payload: null
    });
  }
  function setValidationError(error){
    dispatch({
      type: 'TA_VALIDATION_ERROR',
      payload: error
    });
  }
  function setSuccessMessage(message){
    dispatch({
      type: 'TA_SUCCESS_MESSAGE',
      payload: message
    });
  }
  return (<GlobalContext.Provider value={{
    checklists: state.checklists,
    checklistInstances: state.checklistInstances,
    auditors: state.auditors,
    latests: state.latests,
    error: state.error,
    loading: state.loading,
    validationError: state.validationError,
    successMessage: state.successMessage,
    getAuditors,
    getChecklists,
    getChecklistInstances,
    getLatests,
    addChecklist,
    addChecklistInstance,
    deleteChecklistInstance,
    updateChecklistInstanceStatus,
    resetError, 
    setValidationError,
    setSuccessMessage
    
  }}>
    {children}
  </GlobalContext.Provider>);
}